<template>
	<div class="category">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="center">
				{{title}}
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
				<!--滚动--->
		<scroll class="detail-scroller"
				ref="scroller"
				:probe-type=3
		>
		<!--轮播图-->
			<category-swiper :banners="banners" @swiperImgLoad="swiperImgLoad"/>
			<div class="pd10">
				<!--全部角色之类的按钮-->
				<king-kong-menus/>
				<topics/>
				<multi-function-nav/>
				<!--商品列表-->
				<goods-list :role-arr="roleArr"/>
			</div>
			<div class="button"><a href="#">查看全部</a></div>
		</scroll>
	</div>
</template>

<script>


import NavBar from 'components/common/navbar/NavBar'

import CategorySwiper from './childComps/CategorySwiper'
import KingKongMenus from './childComps/KingKongMenus'
import Topics from './childComps/Topics'
import MultiFunctionNav from './childComps/MultiFunctionNav'
import GoodsList from 'components/contents/goods/GoodsList'
import {getHomeMultiData} from 'network/home'
import {getSwiperData} from 'network/category'


import Scroll from 'components/common/scroll/Scroll'


export default {
  name: 'Category',
  components:{
	NavBar,
	CategorySwiper,
	KingKongMenus,
	Topics,
	MultiFunctionNav,
	GoodsList,
	Scroll
  },
  data(){
	return{
		title:'分类',
		banners:[],
		roleArr:[],
	}
  },
  created(){
	//获取商品信息
	getHomeMultiData().then(res=>{
		this.roleArr.push(...res)
	})
	//获取轮播图
	getSwiperData().then(res=>{
		this.banners.push(...res)
	})
  },
  methods:{
	swiperImgLoad(){
		console.log("图片加载完成")
	}
  }
}
</script>

