<template>
	<div class="king-kong-menus">
		<menus-item v-for="(item,index) in kingArr" :key="index" :item-obj="item" class="box" @click.native="itemClick(index)"/>
	</div>
</template>

<script>

import menusItem from './menusItem'

export default {
  name: 'KingKongMenus',
  components:{
	menusItem
  },
  data(){
	return{
		kingArr:[
			{title:'全部订单',color:'red'},
			{title:'商品管理',color:'green'},
			{title:'客户管理',color:'yellow'},
			{title:'库存管理',color:'orange'}
		]
	}
  },
  methods:{
	itemClick(index){
		switch(index){
			case 0:
				this.$router.push("/allgoods").catch(err=>{err})
				break;
			case 1:
				this.$router.push("/categorygoodlist").catch(err=>{err})
				break;
			case 2:
				this.$router.push("/allgoods").catch(err=>{err})
				break;
			case 3:
				this.$router.push("/allgoods").catch(err=>{err})
				break;
		}
	}
  }
}
</script>
<style>
.king-kong-menus{
	display:flex;
	padding:15px 8px;
	background:#fff;
	border-radius:15px;
	margin-top:10px;
}
.box{
	flex:1;
	text-align:center;
	font-size:14px;
}
</style>
